import React from 'react';

import buttonPic from "../../images/btn_main.jpeg";
import weirddogPic from "../../images/weirddog.png";
import info from "../../images/info.png";
import next from "../../images/triangle.png";
import pic from "../../images/pic.png"
import previous from "../../images/triangle.png";
import Projects from "./contentInfo";
import './content.css';

const ContentHeader = (props) => {
    return (
        <h1 className={"contentTitle"}>{props.title}</h1>
    );
}

const ContentBody = (props) => {
    return (
        <p className={"contentBody"}>{props.body}</p>
    );
}

const ContentPress = (props) => {
    return (
        <>
            <p className={"press"}>Press:</p>
            <span> 
            {props.press.map(pressBlock => (
                <a
                    className={"pressLink"}
                    target="_blank"
                    href={pressBlock.link}
                >
                    {pressBlock.title}
                </a>
            ))}
            </span>
        </>
    );
}

const ContentLink = (props) => {
    return (
        <a
            className={"linkToProject contentProject"}
            target="_blank"
            href={props.linkToProject}
        >
            💥 Check it out here 💥
        </a>
    );
}

const NextProject = ({left}) => {
    return (<div className={left ? "leftContainer": "rightContainer"}>
            <svg id="wave" version="1.1" id="Layer_1" xmlns="http://www.w3.org/2000/svg" x="0px" y="0px"
                viewBox="0 0 456.7 39.9" style={{enableBackground:"new 0 0 456.7 39.9;"}} xmlSpace="preserve">
            <path className="st69" d="M4.2,33.2c0.1-0.1,7-6.9,15.9-13.8C27.7,13.7,38.7,6,47.5,6c7.5,0,14,6.6,20.3,12.9l0.4,0.4
                c6.8,6.9,14.6,14.6,24.6,14.6c9.9,0,17.7-7.8,24.5-14.6l0.5-0.5C124,12.5,130.5,6,137.9,6c7.5,0,13.9,6.5,20.2,12.9l0.4,0.4
                c6.8,6.9,14.6,14.6,24.5,14.6c10,0,17.8-7.8,24.6-14.6l0.5-0.5C214.4,12.5,220.9,6,228.4,6c7.5,0,14,6.5,20.2,12.9l0.4,0.4
                c6.8,6.9,14.5,14.6,24.5,14.6c9.9,0,17.7-7.8,24.5-14.6l0.3-0.3c6.3-6.4,12.9-13,20.5-13c7.5,0,14.1,6.6,20.4,13l0.3,0.3
                c6.8,6.9,14.6,14.6,24.5,14.6c9.9,0,17.6-7.8,24.5-14.6l0.2-0.2C395.1,12.6,401.6,6,409.2,6c8.7,0,19.8,7.7,27.3,13.4
                c8.9,6.8,15.9,13.7,16,13.8"/>
            </svg>
            </div >);
}

const Content = ({currentProjectIndex, handleNextProject, handleShowContentInfo, showContentInfo}) => {
    const projects = {
        0: {
            image: buttonPic
        },
        1: {
            image: weirddogPic
        }
    };

    return (
        <>
            <div className={"prevArrowWrapper"} onClick={handleNextProject}>
                <img className={"previousArrow"} src={previous} />
                <NextProject left={true} />
            </div>
            <div className={"mainContent"} id="content">
                <div className={"imgWrapper"}>
                    {showContentInfo
                        ? <div>
                            <ContentHeader
                                title={Projects.projects[currentProjectIndex].title}
                            />
                            <ContentBody
                                body={Projects.projects[currentProjectIndex].about}
                            />
                            {Projects.projects[currentProjectIndex].press.length > 0 &&
                                <ContentPress
                                    press={Projects.projects[currentProjectIndex].press}
                                />
                            }
                            <ContentLink
                                linkToProject={Projects.projects[currentProjectIndex].linkToProject}
                            />
                        </div>
                        : <img
                            src={projects[currentProjectIndex].image}
                            width={"800px"}
                            className={"image"}
                        />
                    }
                    <img
                        src={!showContentInfo ? info : pic}
                        className={"info"}
                        onClick={handleShowContentInfo}
                    />
                </div>
            </div>
            <div className={"nextArrowWrapper"} onClick={handleNextProject}>
                <NextProject left={false} />
                <img className={"nextArrow"} src={next} />
            </div>
        </>
    );
}

export default Content;