import React, {Component} from 'react';

import {
  About,
  Contact,
  Content,
  HeaderBottom,
  HeaderTop,
  LandingPage
} from "./components";
import './App.css';

class App extends Component {
  state = {
    contentType: "projects",
    currentProjectIndex: 0,
    showContent: false,
    showContentInfo: false
  }

  handleChangeContentType = (content) => {
    this.setState({
      contentType: content
    })
  }

  handleShowContent = () => {
    this.setState({
      showContent: !this.state.showContent
    })
  }

  handleShowContentInfo = () => {
    this.setState({
      showContentInfo: !this.state.showContentInfo
    })
  }

  handleNextProject = () => {
    return this.state.currentProjectIndex === 0
      ? this.setState({currentProjectIndex: 1})
      : this.setState({currentProjectIndex: 0})
  }

  render() {
    return (
      <>
        <HeaderTop handleShowContent={this.handleShowContent} handleChangeContentType={this.handleChangeContentType} />
        {
          this.state.contentType === "projects"&&
            <Content
              handleShowContentInfo={this.handleShowContentInfo}
              handleNextProject={this.handleNextProject}
              showContentInfo={this.state.showContentInfo}
              currentProjectIndex={this.state.currentProjectIndex}
            />
        }
        {
          this.state.contentType === "about" &&
            <About />
        }
        {
          this.state.contentType === "contact" &&
            <Contact />
        }
        <HeaderBottom handleChangeContentType={this.handleChangeContentType} />
        <LandingPage />
      </>
    );
  }
}

export default App;
