import React, {Component} from 'react';
import surfer from "../../images/surf-1.png";

import './header.css';

export class HeaderTop extends Component {
    constructor(props) {
        super(props);
  }

  handleMouseOverText = () => {
      let what = document.getElementById("o");
      what.style.transform = "scale(10, 2)"
  }

  handleMouseLeaveText = () => {
      let what = document.getElementById("o");
      what.style.transform = "scale(1, 1)"
  }

  handleMouseOverTest = () => {
      this.magnify("myimage", 2)
  }
  
  openSesame = () => {
    this.setState({
        menuOpen: !this.state.menuOpen
    })
  }
  render() {
    return (
        <>
            <div className={"header headerTop"}>
                <span className={"projectsText headerTopText"} onClick={() => this.props.handleChangeContentType("projects")}>
                    projects
                </span>
                <span className={"aboutText headerTopText"} onClick={() => this.props.handleChangeContentType("about")}>
                    about
                </span>
            </div>
        </>

    );
  }
}
